import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import UserAPI from "../API/UserAPI";
import { addSession } from "../Redux/Action/ActionSession";
import "./Auth.css";
import queryString from "query-string";
import CartAPI from "../API/CartAPI";
import { useHistory } from "react-router-dom";

function SignIn(props) {
  //listCart c ly t redux
  let history = useHistory();
  const listCart = useSelector((state) => state.Cart.listCart);

  const [email, setEmail] = useState("");

  const [password, setPassword] = useState("");

  const [user, setUser] = useState([]);

  const [errorEmail, setErrorEmail] = useState(false);
  const [emailRegex, setEmailRegex] = useState(false);
  const [errorPassword, setErrorPassword] = useState(false);

  const [redirect, setRedirect] = useState(false);

  const [checkPush, setCheckPush] = useState(false);

  const dispatch = useDispatch();

  // useEffect(() => {
  //   const fetchData = async () => {
  //     const response = await UserAPI.getAllData();

  //     setUser(response);
  //   };

  //   fetchData();
  // }, []);

  const onChangeEmail = (e) => {
    setEmail(e.target.value);
  };

  const onChangePassword = (e) => {
    setPassword(e.target.value);
  };

  const onSubmit = () => {
    if (!email) {
      setErrorEmail(true);
      return;
    } else {
      if (!password) {
        setErrorEmail(false);
        setErrorPassword(true);
        return;
      } else {
        setErrorPassword(false);

        if (!validateEmail(email)) {
          setEmailRegex(true);
          return;
        } else {
          // setEmailRegex(false);

          // const findUser = user.find((value) => {
          // 	return value.email === email;
          // });

          // if (!findUser) {
          // 	setErrorEmail(true);
          // 	return;
          // } else {
          // 	setErrorEmail(false);

          // 	if (findUser.password !== password) {
          // 		setErrorPassword(true);
          // 		return;
          // 	} else {
          // 		setErrorPassword(false);

          // 		localStorage.setItem('id_user', findUser._id);

          // 		localStorage.setItem('name_user', findUser.fullname);

          // 		const action = addSession(localStorage.getItem('id_user'));
          // 		dispatch(action);

          // 		setCheckPush(true);
          // 	}
          // }
          const fetchSignIn = () => {
            const data = {
              email: email,
              password: password,
            };
            UserAPI.postSignIn(data)
              .then((res) => {
                if (res.status === 401) {
                  throw new Error("Validation failed.");
                }
                if (res.status !== 200 && res.status !== 201) {
                  console.log("Error!");
                  throw new Error("Could not authenticate you!");
                }
                return res;
              })
              .then((resData) => {
                console.log(resData);
                // this.setState({
                //   isAuth: true,
                //   token: resData.token,
                //   authLoading: false,
                //   userId: resData.userId,
                // });
                // localStorage.setItem("token", resData.data.token);
                localStorage.setItem("userId", resData.data.userId);
                const remainingMilliseconds = 60 * 60 * 1000;
                const expiryDate = new Date(
                  new Date().getTime() + remainingMilliseconds
                );
                localStorage.setItem("expiryDate", expiryDate.toISOString());
                // this.setAutoLogout(remainingMilliseconds);
                // history.push("/");
                window.location.href = "/";
              })
              .catch((err) => alert(err.message));
          };

          fetchSignIn();
        }
      }
    }
  };

  //Hm ny dng  a ht tt c carts vo API ca user
  useEffect(() => {
    const fetchData = async () => {
      //Ln u s khng thc hin insert c v addCart = ''
      if (checkPush === true) {
        for (let i = 0; i < listCart.length; i++) {
          //N s ly idUser v idProduct v count cn thm  gi ln server
          const params = {
            idUser: localStorage.getItem("id_user"),
            idProduct: listCart[i].idProduct,
            count: listCart[i].count,
          };

          const query = "?" + queryString.stringify(params);

          const response = await CartAPI.postAddToCart(query);
          console.log(response);
        }

        setRedirect(true);
      }
    };

    fetchData();
  }, [checkPush]);

  function validateEmail(email) {
    const re =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  return (
    <div className="limiter">
      <div className="container-login100">
        <div className="wrap-login100 p-l-55 p-r-55 p-t-65 p-b-50">
          <span className="login100-form-title p-b-33">Sign In</span>

          <div className="d-flex justify-content-center pb-5">
            {emailRegex && (
              <span className="text-danger">* Incorrect Email Format</span>
            )}
            {errorEmail && (
              <span className="text-danger">* Please Check Your Email</span>
            )}
            {errorPassword && (
              <span className="text-danger">* Please Check Your Password</span>
            )}
          </div>

          <div className="wrap-input100 validate-input">
            <input
              className="input100"
              name="email"
              type="text"
              placeholder="Email"
              value={email}
              onChange={onChangeEmail}
            />
          </div>

          <div className="wrap-input100 rs1 validate-input">
            <input
              className="input100"
              name="password"
              type="password"
              placeholder="Password"
              value={password}
              onChange={onChangePassword}
            />
          </div>

          <div className="container-login100-form-btn m-t-20">
            {redirect && <Redirect to={`/`} />}
            <button className="login100-form-btn" onClick={onSubmit}>
              Sign in
            </button>
          </div>

          <div className="text-center p-t-45 p-b-4">
            <span className="txt1">Create an account?</span>
            &nbsp;
            <Link to="/signup" className="txt2 hov1">
              Sign up
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SignIn;
